import Vue from "vue";
import VueRouter from "vue-router";
import Home from "@/views/home";
import Information from "@/views/information"
import IntegralRule from "@/views/integralRule"
import TurnoverEnter from "@/views/turnoverEnter"
import Account from "@/views/queryIntegral/account"
import Main from "@/views/Main";


const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}
Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Main',
    redirect: 'home',
    component: Main,
    children: [
      {
        path: 'home',
        name: 'home',
        component: Home,
      },
      {
        path: 'information',
        name: 'information',
        component: Information,
      },
      {
        path: 'integralRule',
        name: 'integralRule',
        component: IntegralRule,
      },
      {
        path: 'turnoverEnter',
        name: 'turnoverEnter',
        component: TurnoverEnter,
      },
      {
        path: 'account',
        name: 'account',
        component: Account,
      },
    ],
  },
];

const router = new VueRouter({
  mode: "history",
  routes,
});

export default router;
