<template>
  <div class="common-table">
    <!-- 页面头部：新增、输入框、查询 -->
    <div class="manage-header">
      <!-- 新增按钮 -->
      <el-button type="primary" @click="addUser">+ 新增</el-button>
      <!-- 使用flex布局 -->
      <div>
        <!-- 文本输入框 -->
        <el-input v-model="name" placeholder="请输入..." clearable></el-input>
        <!-- 搜索按钮 -->
        <el-button type="primary" @click="getList()">搜索</el-button>
      </div>
    </div>
    <!-- 对话框：form 表单 -->
    <el-dialog
      :title="operateType === 'add' ? '新增' : '修改'"
      :visible.sync="isShow"
    >
      <el-form ref="form" label-width="100px" :inline="true">
        <el-form-item
          v-for="item in opertateFormLabel"
          :key="item.model"
          :label="item.label"
        >
          <!-- 文本框 -->
          <!-- 用户名 -->
          <el-input
            v-if="item.model === 'username'"
            :placeholder="'请输入' + item.label"
            v-model="user.username"
          ></el-input>
          <!-- 密码 -->
          <el-input
            v-if="item.model === 'password'"
            :placeholder="'请输入' + item.label"
            v-model="user.password"
          ></el-input>
          <!-- 电话 -->
          <el-input
            v-if="item.model === 'phone'"
            :placeholder="'请输入' + item.label"
            v-model="user.phone"
          ></el-input>
          <!-- 性别 -->
          <el-select
            v-if="item.model === 'gender'"
            placeholder="请选择"
            v-model="user.gender"
          >
            <el-option
              v-for="item in item.options"
              :key="item.value"
              :label="item.label"
              :value="item.label"
            ></el-option>
          </el-select>
          <!-- 出生日期 -->
          <el-date-picker
            v-if="item.model === 'birthday'"
            type="date"
            value-format="yyyy-MM-dd"
            placeholder="选择日期"
            v-model="user.birthday"
          ></el-date-picker>
          <!-- 邮箱 -->
          <el-input
            v-if="item.model === 'email'"
            :placeholder="'请输入' + item.label"
            v-model="user.email"
          ></el-input>
          <!-- 地址 -->
          <el-input
            v-if="item.model === 'address'"
            :placeholder="'请输入' + item.label"
            v-model="user.address"
          ></el-input>
          <!-- 描述 -->
          <el-input
            v-if="item.model === 'description'"
            :placeholder="'请输入' + item.label"
            v-model="user.description"
          ></el-input>
          <!-- 积分 -->
          <el-input
            v-if="item.model === 'integral'"
            :placeholder="'请输入' + item.label"
            v-model="user.integral"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <slot></slot>
        </el-form-item>
      </el-form>
      <!-- dialog底部 -->
      <div slot="footer" class="dialog-footer">
        <el-button @click="isShow = false">取消</el-button>
        <el-button type="primary" @click="confirm">确定</el-button>
      </div>
    </el-dialog>
    <!-- table列表 -->
    <el-table :data="tableData" height="90%" stripe>
      <el-table-column
        show-overflow-tooltip
        v-for="item in tableLabel"
        :key="item.prop"
        :label="item.label"
        :width="item.width ? item.width : 125"
      >
        <template slot-scope="scope">
          <span>{{ scope.row[item.prop] }}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" min-width="180">
        <template slot-scope="scope">
          <el-button size="mini" @click="handleEdit(scope.row)">编辑</el-button>
          <el-button size="mini" type="danger" @click="handleDelete(scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
// import { nanoid } from "nanoid";
import axios from "axios";
export default {
  name: "Information",
  data() {
    return {
      operateType: "add",
      isShow: false,
      user: {
        uid: "",
        username: "",
        password: "",
        phone: "",
        gender: "",
        birthday: "",
        email: "",
        address: "",
        description: "",
        integral: "",
      },
      name: "",
      // dialog框显示的提示信息
      opertateFormLabel: [
        {
          model: "username",
          label: "姓名",
        },
        {
          model: "password",
          label: "密码",
        },
        {
          model: "phone",
          label: "电话",
        },
        {
          model: "gender",
          label: "性别",
          options: [
            {
              label: "男",
              value: 1,
            },
            {
              label: "女",
              value: 0,
            },
          ],
        },
        {
          model: "birthday",
          label: "出生日期",
        },
        {
          model: "email",
          label: "邮箱",
        },
        {
          model: "address",
          label: "地址",
        },
        {
          model: "description",
          label: "描述",
        },
        {
          model: "integral",
          label: "积分",
        },
      ],
      // 初始化dialog框各属性的值为空
      /* operateForm:{
        username: '',
        password: '',
        phone: '',
        gender: '',
        birthday: '',
        email: '',
        address: '',
        description: '',
        integral: '' //积分
      }, */
      // 会员资料数据
      tableData: [
         /* {
          id: "001",
          username: "张三",
          password: "shandong",
          phone: "15897452456",
          gender: "男",
          birthday: "2000-01-01",
          email: "110@163.com",
          address: "山东青岛",
          description: "不积跬步无以至千里",
          integral: "2000",
        },
        {
          id: "002",
          username: "李四",
          password: "shanxi",
          phone: "12548952456",
          gender: "男",
          birthday: "2010-05-015",
          email: "210@163.com",
          address: "陕西西安",
          description: "不积跬步无以至千里",
          integral: "1520",
        },
        {
          id: "003",
          username: "王五",
          password: "beijing",
          phone: "12156252456",
          gender: "男",
          birthday: "2002-04-01",
          email: "310@163.com",
          address: "北京朝阳",
          description: "不积跬步无以至千里",
          integral: "1950",
        },
        {
          id: "004",
          username: "大白",
          password: "shanghai",
          phone: "19542852456",
          gender: "男",
          birthday: "1990-05-01",
          email: "410@163.com",
          address: "上海浦东",
          description: "不积跬步无以至千里",
          integral: "8520",
        },
        {
          id: "005",
          username: "小宏",
          password: "sichuan",
          phone: "13654852456",
          gender: "男",
          birthday: "2008-10-01",
          email: "510@163.com",
          address: "四川成都",
          description: "不积跬步无以至千里",
          integral: "9620",
        }, */
      ],
      // dialog框内容
      tableLabel: [
        {
          prop: "username",
          label: "姓名",
        },
        {
          prop: "password",
          label: "密码",
        },
        {
          prop: "phone",
          label: "电话",
        },
        {
          prop: "gender",
          label: "性别",
        },
        {
          prop: "birthday",
          label: "出生日期",
        },
        {
          prop: "email",
          label: "邮箱",
        },
        {
          prop: "address",
          label: "地址",
        },
        {
          prop: "description",
          label: "描述",
        },
        {
          prop: "integral",
          label: "积分",
        },
      ],
    };
  },
  methods: {
    search() {
      axios({
        method: "get",
        url: "http://localhost:8080/users",
      }).then((resp) => {
        this.tableData = resp.data.data;
      });
    },
    add() {
      axios({
        method: "post",
        url: "http://localhost:8080/users",
        data: this.user,
      }).then((resp) => {
        this.user.username = "";
        this.user.password = "";
        this.user.phone = "";
        this.user.gender = "";
        this.user.birthday = "";
        this.user.email = "";
        this.user.address = "";
        this.user.description = "";
        this.user.integral = "";
        alert(resp.data.msg);

        this.isShow = false;
        this.search()
      });
    },
    // 添加信息
    addUser() {
      // 弹出dialog框
      this.isShow = true;
    },
    // 修改信息
    handleEdit(row) {
      this.operateType = "edit";
      this.isShow = true;
      console.log('row--',row);
      
      axios({
        method: "get",
        url: "http://localhost:8080/users/" + row.uid,
      }).then((resp) => {
        row = resp.data.data;
      });
      this.user.uid = row.uid;
      this.user.username = row.username;
      this.user.password = row.password;
      this.user.phone = row.phone;
      this.user.gender = row.gender;
      this.user.birthday = row.birthday;
      this.user.email = row.email;
      this.user.address = row.address;
      this.user.description = row.description;
      this.user.integral = row.integral;
      // console.log(row);
    },
    confirm() {
      if (this.operateType === "add") {
        
        /* const personObj = {
          id: nanoid(),
          username: this.user.username,
          password: this.user.password,
          gender: this.user.gender,
          phone: this.user.phone,
          birthday: this.user.birthday,
          email: this.user.email,
          address: this.user.address,
          description: this.user.description,
          integral: this.user.integral,
        };
        console.log(personObj);
        this.tableData.unshift(personObj); */

        // this.isShow = false;
        this.add();
        // this.search();
      } else if (this.operateType === "edit") {
        axios({
          method: "put",
          url: "http://localhost:8080/users",
          data: this.user,
        }).then((resp) => {
          this.isShow = false;
          alert(resp.data.msg);

          this.search()
        });
        
        
      }
    },
    // 模糊查询
    getList() {
      /* this.tableData = this.tableData.filter((item) =>
        item.username.includes(this.name)
      ); */
      axios
        .get("http://localhost:8080/users/search/" + this.name)
        .then((resp) => {
          this.tableData = resp.data.data;
        });
    },
    handleDelete(row) {
      axios({
        method: "delete",
        url: "http://localhost:8080/users/" + row.uid,
      }).then((resp) => {
        alert(resp.data.msg)
        this.search()
      });
      
    },
  },
  mounted() {
    this.search();
  },
};
</script>

<style lang="less" scoped>
.el-input {
  width: 200px;
  height: 20px;
}
.manage-header {
  margin-top: 15px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.el-input {
  margin-right: 10px;
}
.el-table {
  margin-top: 70px;
}
.common-table {
  height: calc(100% -62px);
  background-color: #fff;
  position: relative;
  .pager {
    position: absolute;
    bottom: 20;
    right: 20px;
  }
}
</style>
