<template>
  <div>
    <h1>{{ changeTime() }} Admin</h1>
    <h3>欢迎来到 SCRM 管理系统</h3>
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      time: "time",
    };
  },
  methods: {
    changeTime() {
      let timer = new Date();
      let hour = timer.getHours();
      if (hour < 12) {
        return "Good Morning";
      } else if (hour > 12 && hour < 19) {
        return "Good Afternoon";
      } else {
        return "Good Evening";
      }
    },
  },
};
</script>

<style lang="less" scoped>
div {
  margin: 250px auto;
  text-align: center;
}
h1 {
  color: #79bbff;
  font-size: 50px;
}
h3 {
  color: #a0cfff;
  font-size: 30px;
}
</style>
