<template>
  <el-menu
    default-active="1-4-1"
    class="el-menu-vertical-demo"
    background-color="#545c64"
    text-color="#fff"
    active-text-color="skyblue"
    @open="handleOpen"
    @close="handleClose"
    :collapse="isCollapse"
  >
    <h3>{{ isCollapse ? "SCRM 管理系统" : "管理" }}</h3>
    <el-menu-item
      @click="clickMenu(item)"
      v-for="item in noChildren"
      :index="item.path"
      :key="item.path"
    >
      <i :class="'el-icon-' + item.icon"></i>
      {{ item.label }}
    </el-menu-item>
  </el-menu>
</template>

<script>
export default {
  data() {
    return {
      isCollapse: true,
      menu: [
        {
          path: '/home',
          name: 'home',
          label: '首页',
          icon: 's-home',
          url: 'Home/Home',
        },
        {
          path: '/information',
          name: 'information',
          label: '会员资料',
          icon: 'notebook-2',
          url: 'Information/Information',
        },
        {
          path: '/integralRule',
          name: 'integralRule',
          label: '会员积分规则配置',
          icon: 'setting',
          url: 'IntegralRule/IntegralRule',
        },
        {
          path: '/turnoverEnter',
          name: 'turnoverEnter',
          label: "会员消费流水录入",
          icon: 's-goods',
          url:'TurnoverEnter/TurnoverEnter',
        },
        {
          path: '/account',
          name: 'account',
          label: '会员积分余额+台账',
          icon: 's-data',
        },
      ],
    };
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    clickMenu(item){
      this.$router.push({
        name: item.name
      })
      console.log(item.name);
      // this.$store.commit('selectMenu', item)
    }
  },
  computed: {
    noChildren() {
      return this.menu.filter((item) => !item.children);
    }
  },
};
</script>

<style lang="less" scoped>
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}
.el-menu {
  width: 200px;
  height: 100vh;
  border: none;
  h3 {
    color: #fff;
    text-align: center;
    line-height: 48px;
  }
}
</style>
