<template>
  <div>
    请输入会员ID：<el-input v-model="transaction.uid"></el-input><br />
    请输入商品名称：<el-input v-model="transaction.goods"></el-input><br />
    请输入支付方式：<el-input v-model="transaction.way"></el-input><br />
    请输入支付总金额：<el-input v-model="transaction.payment"></el-input><br />
    <el-button type="primary" @click="submit()">确认提交</el-button>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "turnoverEnter",
  data() {
    return {
      transaction: {
        uid: "",
        goods: "",
        payment: "",
        way: "",
      },
    };
  },
  methods: {
    submit() {
      axios({
        method: "post",
        url: "http://localhost:8080/transaction",
        data: this.transaction,
      }).then((resp) => {
        alert(resp.data.msg)
        this.transaction.uid = ''
        this.transaction.goods = ''
        this.transaction.payment = ''
        this.transaction.way = '请输入'
      });
    },
  },
};
</script>

<style lang="less" scoped>
div {
  font-size: 20px;
  color: skyblue;
  margin-top: 50px;
  text-align: center;
}
.el-input {
  width: 200px;
}
.el-button {
  margin-top: 30px;
}
</style>
